/**
 * These are general improvements to the admin theme.
 */
/**
 * Custom variables that are not overrides of Foundation variables.
 */
.contextual-toolbar-tab.toolbar-tab {
  display: none !important; }

thead, tbody, tfoot {
  border: 1px solid #CCCCCC; }

form .form-composite > legend, form .label {
  color: #333333; }

form label,
form .form-item label {
  margin-bottom: 5px; }

form .form-item .description {
  margin-top: 5px; }

form .form-type-checkbox label {
  font-weight: bold; }

form .form-type-radio .description,
form .form-type-checkbox .description {
  margin-left: 24px; }

form .field--widget-text-textarea .form-type-textarea.js-form-item,
form .field--widget-text-textarea-with-summary .form-type-textarea.js-form-item {
  margin-bottom: 0; }

form .field--widget-text-textarea .cke_contents,
form .field--widget-text-textarea-with-summary .cke_contents {
  padding: 10px;
  background-color: #F5F5F5; }

form .field-multiple-table .draggable > td {
  padding: 10px; }

form .field-multiple-table .draggable .field-multiple-drag {
  padding: 0; }

form .field-multiple-table .draggable a.tabledrag-handle {
  height: 100%;
  margin-left: 0;
  padding: 13px 10px !important; }

form .field-multiple-table .draggable a.tabledrag-handle .handle {
  width: 22px;
  height: 22px;
  margin: 0;
  padding: 0;
  background-size: contain;
  background-position: center; }

form .field-multiple-table .draggable > td > .form-item:first-child:last-child {
  margin-bottom: 0; }

form .seven-details {
  border-radius: 0; }

.layout-node-form .form-item,
.layout-node-form tr.odd .form-item,
.layout-node-form tr.even .form-item {
  margin: 0 0 15px 0; }

.layout-node-form .field--widget-text-textarea .form-type-textarea.js-form-item,
.layout-node-form .field--widget-text-textarea-with-summary .form-type-textarea.js-form-item {
  margin-bottom: 0; }

.field--widget-entity-reference-paragraphs {
  margin: 20px 0; }

.paragraph-type-title {
  padding-top: 5px; }

.js .paragraph-type-title {
  flex-basis: auto;
  font-weight: bold; }

.js .paragraph-type-label {
  font-weight: bold; }

.paragraphs-dropbutton-wrapper {
  padding-right: 10px; }

.paragraphs-subform {
  padding-right: 10px; }

.paragraph {
  padding-right: 10px; }

.paragraph--view-mode--preview {
  padding: 10px 10px 0 0; }
